import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/pages/login'
import Dashboard from '@/components/dashboard'
import Products from '@/components/products'
import Home from '@/components/Home'


Vue.use(Router)

export default new Router({
  routes: [
	{
		path: '*',
		redirect:'login',
	},
	{
		path: '/admin',
		name: 'Dashboard',	
		component: Dashboard,
		meta: { 
			requiresAuth: true 
		},
		children:[
			{
				path: 'products',
				name: 'products',
				component: Products,
				meta: { 
					requiresAuth: true 
				},
			},
		],
	},
    {
		path: '/login',
		name: 'login',
		component: Login
	},
  ]
})



