<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openmodal(true)">建立新產品</button>
    </div>   
    <p class="" style="color:red;">API [POST]:已關閉，只能GET</p>      
    <table class="table mt-4">
      
      <thead>
        <tr>
          <th width="120">分類</th>
          <th>產品名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="100">是否啟用</th>
          <th width="80">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">
            {{item.origin_price | currency}}
          </td>
          <td class="text-right">
            {{item.price | currency}}
          </td>
          <td>
            <span v-if="item.is_enabled==1" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openmodal(false,item)">編輯</button>
          </td>
        </tr>
      </tbody>
    </table>
<nav aria-label="Page navigation example">
  <ul class="pagination">
    <li class="page-item" :class="{'disabled': !pagination.has_pre}">
      <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page -1)">
        <span aria-hidden="true">&laquo;</span>
        <span class="sr-only">Previous</span>
      </a>
    </li>
    <li class="page-item" v-for="page in pagination.total_pages" :class="{'active': pagination.current_page===page}" :key="page"><a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a></li>
    <li class="page-item" :class="{'disabled': !pagination.has_next}">
      <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page +1)">
        <span aria-hidden="true">&raquo;</span>
        <span class="sr-only">Next</span>
      </a>
    </li>
  </ul>
</nav>
<!-- Modal -->
<div class="modal fade" id="productsModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-lg" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-dark text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span>新增產品</span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-sm-4">
            <div class="form-group">
              <label for="image">輸入圖片網址</label>
              <input type="text" v-modal="tempproducts.image" class="form-control" id="image"
                placeholder="請輸入圖片連結">
            </div>
            <div class="form-group">
              <label for="customFile">或 上傳圖片
                <i class="fas fa-spinner fa-spin"></i>
              </label>
              <input type="file" id="customFile" class="form-control"
                ref="files" @change="uploadFile">
            </div>
            <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
              :src="tempproducts.image" class="img-fluid" alt="">
          </div>
          <div class="col-sm-8">
            <div class="form-group">
              <label for="title">標題</label>
              <input type="text" class="form-control" v-modal="tempproducts.title" id="title"
                placeholder="請輸入標題">
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="category">分類</label>
                <input type="text" class="form-control" v-modal="tempproducts.category" id="category"
                  placeholder="請輸入分類">
              </div>
              <div class="form-group col-md-6">
                <label for="price">單位</label>
                <input type="unit" class="form-control" id="unit" v-modal="tempproducts.unit"
                  placeholder="請輸入單位">
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
              <label for="origin_price">原價</label>
                <input type="number" class="form-control" id="origin_price" v-modal="tempproducts.origin_price"
                  placeholder="請輸入原價">
              </div>
              <div class="form-group col-md-6">
                <label for="price">售價</label>
                <input type="number" class="form-control" id="price" v-modal="tempproducts.price"
                  placeholder="請輸入售價">
              </div>
            </div>
            <hr>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  id="is_enabled"
                  v-modal="tempproducts.is_enabled" :true-value="1" :false-value="1">
                <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
        <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
      </div>
    </div>
  </div>
</div>


  </div>
</template>  

<script>

import $ from 'jquery';
export default {
  data(){
    return{
      products:[],
      pagination:{},
      tempproducts:[],
      isNew: false,
      isLoading: false,
    };
  },
  methods:{
    getProducts(page=1){
        const api =`https://vue-course-api.hexschool.io/api/frankguoo/products?page=${page}`;
        const vm = this;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
          vm.isLoading = false;
            if(response.data.success){
                console.log(response.data)
                vm.products = response.data.products;
                vm.pagination = response.data.pagination;
            }else{
                alert('商品載入失敗');
            }
        });
    },
    openmodal(isNew, item){
      
      if(isNew){
        this.tempproducts={};
        this.isNew=true;
      }else{
        this.tempproducts=Object.assign({}, item);
        isNew=false
      }
      $('#productsModal').modal('show');
    },
    updateProduct(){
      let api ='https://vue-course-api.hexschool.io/api/frankguoo/products';
      let httpMethod = 'post';
      const vm = this;
      if(!vm.isNew){
        let api ='https://vue-course-api.hexschool.io/api/frankguoo/admin/products/${vm.tempproducts.id}';
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, {data: vm.tempproducts}).then((response) => {
        console.log(response.data)
          if(response.data.success){
            $('#productsModal').modal('hide');
            vm.getProducts();
          }else{
            $('#productsModal').modal('hide');
            vm.getProducts();
            alert('上傳失敗');
          }
      });
    },
    uploadFile(){
      console.log(this.$refs.files.files);
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const url= "https://vue-course-api.hexschool.io/api/frankguoo/admin/upload";
      this.$http.post(url, uploadedFile ,{
        headers:{
          'Content-type': 'multipart/form-data'
        }
      }).then((response) => {
          if(response.data.success){  
            // vm.tempproducts.image = response.data.image;
            vm.$set(vm.tempproducts, 'image', response.data.image)
          }else{
              alert('圖片上傳失敗');
          }
      });
    }
  },
  created(){
    this.getProducts();
  }
}
</script>
    



